import { Component, OnDestroy, OnInit } from '@angular/core';
import { BasePage } from '../base/base.page';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InfoPopoverComponent } from '../@shared/components/info-popover/info-popover.component';
import { PopoverController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { UserState } from '../@store/user/user.state';
import { Utils } from '../@global/utils';
import { IUserInfo } from '../@store/user/user.model';
import { Subscription } from 'rxjs';
import { UpdateUserInput } from '../../API';
import { SetUserInfo } from '../@store/user/user.actions';
import { take } from 'rxjs/operators';

@Component({
  selector: 'app-privacy',
  templateUrl: './privacy.page.html',
  styleUrls: ['./privacy.page.scss'],
})
export class PrivacyPage extends BasePage implements OnInit, OnDestroy {
  public formGroup: FormGroup;
  private getUserInfoSubscription$: Subscription;
  public userInfo: IUserInfo;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private formBuilder: FormBuilder,
    private popoverController: PopoverController,
    private translateService: TranslateService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.getUserInfoSubscription$ = this.store.select(UserState.getUserInfo).subscribe((data) => {
      this.userInfo = Utils.copyObject(data) as IUserInfo;
    });
    this.formGroup = this.formBuilder.group({
      privacy: [this.userInfo?.private_profile ? 'private' : 'public', Validators.required],
    });
  }

  public async presentPopover($event, option) {
    const text = this.translateService.instant(`privacy.info${option}`);

    const popover = await this.popoverController.create({
      event: $event,
      component: InfoPopoverComponent,
      cssClass: 'custom-alert',
      translucent: true,
      componentProps: { text },
    });
    return await popover.present();
  }

  public async changePrivacy() {
    const data = { private_profile: this.formGroup.get('privacy').value !== 'public' };
    this.store
      .dispatch(new SetUserInfo({ userInfo: data }))
      .pipe(take(1))
      .subscribe(
        () => {
          this.goBack();
        },
        (error: any) => {
          console.log(error);
        }
      );
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  private disconnectObservers(): void {
    this.getUserInfoSubscription$.unsubscribe();
  }
}
